'use client';
import React, { useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { 
  LayoutDashboard, 
  Users, 
  Calendar, 
  Stethoscope, 
  Pill, 
  Bed, 
  Settings,
  LogOut,
  UserCog,
  UsersRound,
  ChevronLeft,
  ChevronRight
} from 'lucide-react';

interface NavItem {
  href: string;
  label: string;
  icon: React.ReactNode;
  bg: string;
}

const Sidebar: React.FC = () => {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const pathname = usePathname();

  const navItems: NavItem[] = [
    { href: '/dashboard', label: 'Dashboard', icon: <LayoutDashboard size={20} className="text-primary-500" />, bg: 'primary' },
    { href: '/patients', label: 'Patients', icon: <Users size={20} className="text-success-500" />, bg: 'success' },
    { href: '/doctors', label: 'Doctors', icon: <Stethoscope size={20} className="text-warning-500" />, bg: 'warning' },
    { href: '/staff', label: 'Staff', icon: <UsersRound size={20} className="text-danger-500" />, bg: 'danger' },
    { href: '/appointments', label: 'Appointments', icon: <Calendar size={20} className="text-primary-500" />, bg: 'primary' },
    { href: '/workstation', label: 'Workstation', icon: <UserCog size={20} className="text-success-500" />, bg: 'success' },
    { href: '/pharmacy', label: 'Pharmacy', icon: <Pill size={20} className="text-warning-500" />, bg: 'warning' },
    { href: '/beds', label: 'Bed Management', icon: <Bed size={20} className="text-danger-500" />, bg: 'danger' },
  ];

  return (
    <aside className={`${isCollapsed ? 'w-20' : 'w-64'} bg-gray-100 h-full flex flex-col shadow-neu-flat transition-all duration-300`}>
      {/* Logo */}
      <div className="flex items-center justify-between h-16 border-b border-gray-200 px-4">
        <div className={`flex items-center ${isCollapsed ? 'justify-center w-full' : ''}`}>
          <div className="neu-icon bg-primary-100">
            <Stethoscope className="text-primary-500" size={24} /> 
          </div>
          {!isCollapsed && <span className="ml-2 text-xl font-medium text-gray-900">Annam</span>}
        </div>
        <button 
          onClick={() => setIsCollapsed(!isCollapsed)}
          className="neu-icon bg-gray-200 hover:bg-gray-300"
        >
          {isCollapsed ? 
            <ChevronRight size={20} className="text-gray-600" /> : 
            <ChevronLeft size={20} className="text-gray-600" />
          }
        </button>
      </div>
      
      {/* Navigation */}
      <nav className="flex-1 p-4 space-y-2">
        {navItems.map((item) => {
          const isActive = pathname === item.href;
          return (
            <Link
              key={item.href}
              href={item.href}
              className={`nav-link ${isActive ? 'active' : ''}`}
            >
              <div className={`neu-icon bg-${item.bg}-100`}>{item.icon}</div>
              {!isCollapsed && <span>{item.label}</span>}
            </Link>
          );
        })}
      </nav>
      
      {/* Bottom Actions */}
      <div className="p-4 border-t border-gray-200">
        <button className="nav-link w-full">
          <div className="neu-icon bg-gray-200">
            <Settings size={20} className="text-gray-600" />
          </div>
          {!isCollapsed && <span>Settings</span>}
        </button>
        
        <button className="nav-link w-full mt-2">
          <div className="neu-icon bg-danger-100">
            <LogOut size={20} className="text-danger-500" />
          </div>
          {!isCollapsed && <span className="text-danger-500">Logout</span>}
        </button>
      </div>
    </aside>
  );
};

export default Sidebar; 